@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family:'Montserrat', sans-serif ,'Helvetica Neue';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.Login_form{
    background: rgba( 255, 255, 255, 0.35 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 2.0px );
    -webkit-backdrop-filter: blur( 2.0px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    width: 25vw;
    height:50vh ;
    border-radius: 30px;
    border: none;
    position: absolute;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-right: 12.5%;
}

.Login_form>span>input,button{
    height: 2.5rem;
    width: 75%;
    border: 4px solid #39386f;
    border-radius: 5px;
    font-size: 1rem;
    background: none;
    color: black;
    
}

.Login_form>span>input:focus-visible{
    outline: none;
}
.Login_form>span{
    display: -webkit-flex;
    display: flex;
    -webkit-align-content: center;
            align-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    width: 100%;
    height: 14%;
    -webkit-align-items: center;
            align-items: center;
}
.Login_form>span>button:hover{
    cursor: pointer;
}

.Login_form>span>label{
    font-size: 1.35rem;
    font-weight: 500 ;
    padding: 2px;
}
.rme{
    font-size: 1.25rem;
}
.rme ,.fn>span{ /* remember me ,forgot your passsword and register link */
    padding:6px;
